import CircleProgress from "@/components/circleProgress";
import { fetchCast, fetchCrew, fetchMovieDetail } from "@/lib/fetchData";
import Link from "next/link";
import {
  HeartFilledIcon,
  BookmarkFilledIcon,
  ListBulletIcon,
  PlayIcon,
} from "@radix-ui/react-icons";
import { formatDate } from "@/lib/birthdayCalculate";

// TMDB image base URLs
const TMDB_IMAGE = {
  backdrop: "https://image.tmdb.org/t/p/w1280",
  poster: "https://image.tmdb.org/t/p/w185",
  profile: "https://image.tmdb.org/t/p/w185",
};

type PageProps = {
  params: Promise<{ id: number }>;
};

export default async function Movie({ params }: PageProps) {
  const { id } = await params;

  // TMDB-style parallel fetching
  const [movie, cast, crew] = await Promise.all([
    fetchMovieDetail(id),
    fetchCast(id),
    fetchCrew(id),
  ]);

  const userScore = Math.round(movie.vote_average * 10);

  // TMDB crew logic
  const director = crew.find((p) => p.job === "Director");
  const writers = crew.filter((p) => p.job === "Writer");

  return (
    <div>
      {/* Backdrop */}
      <div className="relative w-full bg-[#355f61] md:bg-transparent text-white">
        {/* Backdrop */}
        <div className="relative h-44 sm:h-60 md:h-[520px]">
          <img
            src={`${TMDB_IMAGE.backdrop}${movie.backdrop_path}`}
            alt={movie.title}
            className="w-full h-full object-cover"
          />

          {/* Overlay */}
          <div className="absolute inset-0 bg-gradient-to-r from-black/90 via-black/70 to-black/20" />

          {/* Poster */}
          <img
            src={`${TMDB_IMAGE.poster}${movie.poster_path}`}
            alt={movie.title}
            className="
        absolute
        left-4
        bottom-[-32px]
        w-24
        sm:w-32
        md:w-60
        rounded-xl
        shadow-xl
      "
          />
        </div>

        {/* Content */}
        <div className="relative max-w-6xl mx-auto px-4 sm:px-6 pt-10 md:pt-16 pb-6">
          <div className="md:ml-72">
            {/* Title */}
            <h1 className="text-lg sm:text-2xl md:text-4xl font-semibold leading-tight">
              {movie.title}
              <span className="ml-2 font-light text-base sm:text-lg md:text-2xl">
                ({movie.release_date.split("-")[0]})
              </span>
            </h1>

            {/* Genres */}
            <div className="mt-2 text-xs sm:text-sm text-gray-200">
              {movie.genres.map((genre, i) => (
                <Link
                  key={genre.id}
                  href={`/genre/${genre.name}/${genre.id}`}
                  className="hover:text-white"
                >
                  {genre.name}
                  {i < movie.genres.length - 1 && ", "}
                </Link>
              ))}
            </div>

            {/* Score */}
            <div className="flex flex-wrap items-center gap-4 mt-4">
              <div className="flex items-center gap-3">
                <CircleProgress value={userScore} />
                <span className="text-sm font-semibold leading-tight">
                  User <br /> Score
                </span>
              </div>

              <button className="bg-[#032541] px-4 py-2 rounded-full text-sm font-semibold hover:scale-105 transition">
                What&apos;s your{" "}
                <span className="border-b-2 border-[#01B4E4]">Vibe</span>?
              </button>
            </div>

            {/* Meta */}
            <div className="flex flex-wrap items-center gap-3 text-xs sm:text-sm text-gray-200 mt-3">
              <span className="border px-1 rounded">NC16</span>
              <span>{formatDate(movie.release_date)}</span>
              <button className="flex items-center gap-1 font-semibold hover:underline">
                â–¶ Play Trailer
              </button>
            </div>

            {/* Overview */}
            <h2 className="text-base sm:text-lg font-semibold mt-5">
              Overview
            </h2>
            <p className="max-w-3xl text-sm sm:text-base leading-relaxed text-gray-200">
              {movie.overview}
            </p>

            {/* Crew */}
            <div className="grid grid-cols-1 sm:grid-cols-2 gap-x-16 gap-y-5 mt-6 text-sm">
              {director && (
                <div>
                  <p className="font-semibold">{director.name}</p>
                  <p className="text-xs text-gray-300">Director</p>
                </div>
              )}

              {writers.slice(0, 2).map((writer) => (
                <div key={writer.id}>
                  <p className="font-semibold">{writer.name}</p>
                  <p className="text-xs text-gray-300">{writer.job}</p>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>

      {/* Cast */}
      <section className="w-full max-w-[1440px] mx-auto px-6 mt-12">
        <h2 className="text-2xl font-bold mb-6">Top Billed Cast</h2>

        <div
          className="grid gap-6 
                  grid-cols-2 
                  sm:grid-cols-3 
                  md:grid-cols-4 
                  lg:grid-cols-6 
                  xl:grid-cols-7"
        >
          {cast.map((person) => (
            <div
              key={person.id}
              className="flex flex-col items-center text-center"
            >
              <Link href={`/person/${person.id}`} className="group w-[185px]">
                {person.profile_path ? (
                  <img
                    src={`${TMDB_IMAGE.profile}${person.profile_path}`}
                    alt={person.name}
                    className="w-[185px] h-[278px] object-cover rounded-xl 
                         transition-transform duration-200 
                         group-hover:scale-[1.02]"
                  />
                ) : (
                  <div className="w-[185px] h-[278px] bg-gray-300 rounded-xl" />
                )}

                <p className="mt-3 font-semibold leading-tight group-hover:underline">
                  {person.name}
                </p>
              </Link>

              <p className="text-sm text-gray-600 mt-1 leading-snug">
                {person.character}
              </p>
            </div>
          ))}
        </div>
      </section>
    </div>
  );
}
